import React from 'react'
import FullScreenBox from "../components/FullScreenBox";
import CenteredBox from "../components/CenteredBox";
import GradientBox from "../components/GradientBox";
import TextBox from "../components/TextBox";
import styled from "styled-components";
import { Flex, Box, Text } from 'rebass';

const HeaderText = styled.div`
  color: white;
  font-family: 'Ubuntu', sans-serif;
  font-size: xx-large;
  margin-top: 20px;
  text-align: center;
  width: 100%;
`;


const SubText = styled.div`
  color: white;
  font-family: 'Ubuntu', sans-serif;
  text-align: center;
  width: 100%;
  padding: 10px;
`;

const Contract1Text = styled.div`
  color: white;
  font-family: 'Ubuntu', sans-serif;
  margin-top: 10px;
  margin-bottom: 16px;
  text-align: center;
  width: 100%;
  padding: 10px;
`;

const ContractText = styled.div`
  color: white;
  font-family: 'Ubuntu', sans-serif;
  margin-bottom: 16px;
  text-align: center;
  width: 100%;
  padding: 10px;
`;

const Image = styled.img`
  color: white;
  position: relative;
  margin-top: 10vh;
  width: 40%;
`;

const Holder = styled.div`
  margin-top: 16px;
  margin-bottom: 16px;

`

function MainContainer() {
  return (
    <>
      <FullScreenBox>
        <CenteredBox>
          <Image
            src='kucoin-money-brand.png'
          />
          <HeaderText>KuCoin Money</HeaderText>
          <Holder>
          <SubText>Introducing the $KUM Token.</SubText>
          <SubText>Add some Kreme to your $KOFFEE.</SubText>
          </Holder>
          <Flex>
            <Box>
              <SubText>
                Reflective
                <div>
                  5%
                </div>
              </SubText>
              <Contract1Text>
                5% of each KUM swap is redistributed to holders
              </Contract1Text>
            </Box>
            <Box>
            <SubText>
                Liquidity Pool
                <div>
                  5%
                </div>
            </SubText>
              <Contract1Text>
                5% of each KUM swap is redistributed to holders
              </Contract1Text>
            </Box>
          </Flex>
          <ContractText>Contract address: 0x7ea290a4bd6acfc1673822048df40f0a86983139</ContractText>
        </CenteredBox>
        <GradientBox />
      </FullScreenBox>
    </>
  );
}


export default MainContainer;
